import { collection, getDocs } from "firebase/firestore"; 
import { useEffect, useState } from "react";
import { db } from "../firebase_setup/firebase";



const ReadPlayerList =  ()=>{

    // let content = "test";
    const [content, setContent] = useState("Loading top players...");

    useEffect(
        ()=>{

            const temp_user_list = [];
            const temp = async ()=>{
                const querySnapshot = await getDocs(collection(db, "players"));
                querySnapshot.forEach((doc) => {
                    const current_user = {
                        name: doc.data().name,
                        used_time : doc.data().used_time
                    }
                    temp_user_list.push(current_user);
                });

                // content = 
                setContent(
                    <div>
                       <table className="record-table">
                            <caption className="table-title">Top Players</caption>
                            <tr><th>No.</th><th>Name</th><th>Second(s)</th></tr>
                            {
                                temp_user_list.map(
                                    (user, index)=>
                                    {
                                    return  <tr><td>{index+1}</td> <td>{user["name"]}</td> <td>{user["used_time"]}</td></tr>
                                    }
                            
                                )
                            }
                        </table>
                    </div>
                );
                
            }
            temp();
        },[]
    )

    return <div>{content}</div>;
}

export default ReadPlayerList;